.banner{
    margin: 0px;
    margin-bottom: 30px;
    display: flex;
    flex-wrap: nowrap;
    position: relative;
    width: 100%;
    height: 111px;
    @include mq(tablet){
        height: 223px;
        margin: 30px 0px;  
    }

    &__image{
        display: flex;
        width: 100%;
        object-fit: cover;
        border-radius: 25px;
        position: absolute;
        height: 100%;
    }
    &__title{
        margin: 0;
        height: 40%;
        text-align: left;
        padding-left: 20px;
        width: 100%;
        padding-right: 30%;
        position: relative;
        background: none; 
        margin: auto;

        @include mq(tablet){
        height: 25%;
        text-align: center;
        padding: 0;
        }
    }
}